"use client";

import React, { useRef, useEffect, useState } from "react";
import Image from "next/image";
import { motion, useScroll, useTransform } from "motion/react";
import Link from "next/link";

// Process stages data
const processStages = [
  {
    number: "01",
    title: "SKETCH DESIGN",
    image: "/img/p3.avif",
    description: "We kick off with a site visit and creative consultation to define your vision, style, and functional needs. We'll also uncover new opportunities and potential limitations. Based on this, we craft a series of hand-drawn design sketches to provide an initial visual concept that reflects your brief. Through collaboration and refinement, we shape the concept to align with your goals, timeline, and budget."
  },
  {
    number: "02",
    title: "DESIGN DEVELOPMENT",
    image: "/img/p5.jpg",
    description: "Building on the approved sketch design, we develop detailed drawings and 3D models. This stage involves refining spatial relationships, material selections, and technical solutions. We work closely with you to ensure every detail aligns with your vision while meeting all regulatory and performance requirements."
  },
  {
    number: "03",
    title: "DOCUMENTATION",
    image: "/img/p5.jpg",
    description: "We prepare comprehensive construction documentation including detailed drawings, specifications, and schedules. This stage ensures all design intent is clearly communicated to builders and trades, providing the foundation for accurate pricing and quality construction."
  },
  {
    number: "04",
    title: "APPROVALS",
    image: "/img/p3.avif",
    description: "We navigate the regulatory approval process, preparing and submitting all required documentation to local authorities. Our experience ensures efficient processing while maintaining design integrity throughout the approval journey."
  },
  {
    number: "05",
    title: "TENDER & CONTRACT",
    image: "/img/p5.jpg",
    description: "We assist in selecting the right builder through a competitive tender process. Our detailed documentation ensures accurate pricing and we provide guidance throughout contract negotiations to protect your interests."
  },
  {
    number: "06",
    title: "CONSTRUCTION",
    image: "/img/p5.jpg",
    description: "During construction, we provide ongoing support and regular site visits to ensure the build aligns with the design intent. We're your advocate throughout the construction process, addressing any issues that arise and ensuring quality outcomes."
  }
];

export default function Process() {
  const containerRef = useRef<HTMLDivElement>(null);
  const processSectionRef = useRef<HTMLDivElement>(null);
  const [activeIndex, setActiveIndex] = useState(0);

  const { scrollYProgress } = useScroll({
    target: containerRef,
    offset: ["start start", "end end"]
  });

  // Parallax effects
  const heroParallax = useTransform(scrollYProgress, [0, 0.3], [0, -100]);
  const overviewParallax = useTransform(scrollYProgress, [0.2, 0.5], [0, -50]);

  // Track active card based on scroll
  useEffect(() => {
    const handleScroll = () => {
      if (!processSectionRef.current) return;
      
      const section = processSectionRef.current;
      const sectionTop = section.getBoundingClientRect().top;
      const sectionHeight = section.offsetHeight;
      const windowHeight = window.innerHeight;
      
      if (sectionTop > 0 || sectionTop < -sectionHeight) return;
      
      const scrollPercent = Math.abs(sectionTop) / (sectionHeight - windowHeight);
      const index = Math.min(
        processStages.length - 1,
        Math.floor(scrollPercent * processStages.length)
      );
      
      setActiveIndex(index);
    };

    window.addEventListener("scroll", handleScroll, { passive: true });
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <main ref={containerRef} className="bg-white">
      {/* Hero Section */}
      <section className="min-h-screen relative flex items-center">
        <motion.div
          className="w-full px-8 md:px-16"
          style={{ y: heroParallax }}
        >
          <div className="mx-auto grid grid-cols-1 lg:grid-cols-2 gap-16 items-center">
            {/* Left side - Text */}
            <motion.div
              className="space-y-8"
              initial={{ opacity: 0, x: -50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 1, delay: 0.8 }}
            >
              <h3 className="text-sm uppercase tracking-widest text-black mb-8">
                (PROCESS)
              </h3>
              
              <h1 className="text-4xl md:text-5xl font-bold text-black leading-tight">
                Great architecture isn't just about talent and experience, but collaborations and relationships.
              </h1>
            </motion.div>

            {/* Right side - Image */}
            <motion.div
              className="aspect-video relative overflow-hidden"
              initial={{ opacity: 0, x: 50 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ duration: 1, delay: 1.2 }}
            >
              <Image
                src="/img/p1.jpg"
                alt="Modern architecture exterior"
                fill
                className="object-cover"
              />
            </motion.div>
          </div>
        </motion.div>
      </section>

      {/* Our Approach Section */}
      <section className="py-20 px-8 md:px-16 bg-gray-50">
        <div className="max-w-7xl mx-auto grid grid-cols-1 lg:grid-cols-2 gap-12">
          {/* Left side - Title */}
          <motion.div
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            viewport={{ once: true }}
          >
            <h3 className="text-sm uppercase tracking-widest text-black">
              (OUR APPROACH)
            </h3>
          </motion.div>
          
          {/* Right side - Content */}
          <motion.div
            className="space-y-8"
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            viewport={{ once: true }}
          >
            <p className="text-2xl md:text-3xl text-black leading-relaxed">
              You can expect our team to expertly guide your project and work closely with you at every stage from delivering the initial design concepts to achieving a final build that goes beyond your aspirations
            </p>

            <motion.div
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 0.3 }}
              viewport={{ once: true }}
            >
              <Link
                href="/contact"
                className="inline-block bg-black text-white px-8 py-3 text-sm uppercase tracking-widest hover:bg-gray-800 transition-colors duration-300"
              >
                Send us an enquiry
              </Link>
            </motion.div>
            
            {/* Copyright positioned to the far right */}
            <motion.div
              className="text-right text-sm text-gray-500 mt-12"
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 0.5 }}
              viewport={{ once: true }}
            >
              Â© 2025
            </motion.div>
          </motion.div>
        </div>
      </section>

      {/* Overview Section with Dark Background */}
      <section className="relative min-h-screen bg-black text-white overflow-hidden">
        {/* Background Image with parallax */}
        <motion.div 
          className="absolute inset-0"
          initial={{ scale: 1.2 }}
          whileInView={{ scale: 1 }}
          transition={{ duration: 1.5 }}
          viewport={{ once: true }}
        >
          <Image
            src="/img/p2.avif"
            alt="Architecture detail"
            fill
            className="object-cover opacity-60"
          />
          <div className="absolute inset-0 bg-black/50" />
        </motion.div>

        {/* Content */}
        <motion.div
          className="relative z-10 flex items-start min-h-screen px-8 md:px-16 pt-32"
          style={{ y: overviewParallax }}
        >
          <div className="max-w-4xl">
            <motion.div
              initial={{ opacity: 0, y: 50 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              viewport={{ once: true }}
            >
              <h2 className="text-6xl md:text-8xl font-black text-white leading-none mb-8">
                OVERVIEW OF<br />
                OUR 6-STAGE<br />
                PROCESS
              </h2>
            </motion.div>

            {/* Scroll indicator with brackets (no circle) */}
            <motion.div
              className="absolute bottom-12 right-12"
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 1 }}
              viewport={{ once: true }}
            >
              <div className="flex items-center text-white text-4xl">
                <span className="mr-1">(</span>
                <svg 
                  className="w-12 h-12 text-white" 
                  fill="none" 
                  stroke="currentColor" 
                  viewBox="0 0 24 24"
                >
                  <path 
                    strokeLinecap="round" 
                    strokeLinejoin="round" 
                    strokeWidth={2} 
                    d="M19 14l-7 7m0 0l-7-7m7 7V3" 
                  />
                </svg>
                <span className="ml-1">)</span>
              </div>
            </motion.div>
          </div>
        </motion.div>
      </section>

      {/* Process Stages with sticky scroll */}
      <section ref={processSectionRef} className="relative bg-black min-h-[600vh]">
        <div className="sticky top-0 h-screen overflow-hidden">
          {processStages.map((stage, index) => (
            <ProcessCard 
              key={index} 
              {...stage} 
              index={index} 
              isActive={index === activeIndex} 
              isPrev={index < activeIndex}
            />
          ))}
        </div>
      </section>
    </main>
  );
}

type ProcessCardProps = {
  number: string;
  title: string;
  description: string;
  image: string;
  index: number;
  isActive: boolean;
  isPrev: boolean;
};

function ProcessCard({
  number,
  title,
  description,
  image,
  index,
  isActive,
  isPrev
}: ProcessCardProps) {
  return (
    <motion.div 
      className={`absolute inset-0 flex items-center justify-center px-8 md:px-16 transition-opacity duration-500 ${isActive ? "z-30" : isPrev ? "z-20" : "z-10"}`}
      initial={{ y: "100%", opacity: 0 }}
      animate={{ 
        y: isActive ? "0%" : isPrev ? "-100%" : "100%",
        opacity: isActive ? 1 : isPrev ? 0 : 0
      }}
      transition={{ type: "spring", damping: 30, stiffness: 200 }}
    >
      <div className="card-inner w-full h-[90%] bg-white p-6 lg:p-12 shadow-2xl transform origin-center">
        <div className="flex flex-row justify-between mb-8 lg:mb-12">
          <h3 className="text-[clamp(36px,6vw,120px)] font-semibold tracking-tight leading-none text-black">
            {title}
          </h3>

          <p className="text-lg lg:text-[clamp(36px,6vw,120px)] mt-2 lg:mt-0 font-semibold tracking-wider lg:tracking-normal leading-none text-black opacity-20">
            ({number})
          </p>
        </div>

        <div className="flex flex-col-reverse lg:flex-row items-start justify-between w-full h-3/4">
          <div className="flex flex-col gap-6 lg:gap-8 w-full lg:w-6/12 h-full justify-center">
            <p className="text-[clamp(16px,1.8vw,32px)] font-semibold leading-relaxed text-black">
              {description}
            </p>
          </div>

          <div className="relative w-full lg:w-5/12 h-full rounded-2xl overflow-hidden mb-4 lg:mb-0">
            <Image
              src={image}
              alt={title}
              fill
              className="object-cover object-center"
            />
          </div>
        </div>
      </div>
    </motion.div>
  );
}